html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Arial', 'Helvetica', sans-serif;
}

body,
#root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.app {
  height: 100%;
  width: 100%;
  margin: 0;
  overflow: hidden;
  display: flex;
  background: #f4f5f7;
}

svg.icon {
  fill: none;
  stroke: #343539;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width:2;
}

svg.icon.big {
  width: 80%;
}

button:disabled svg.icon {
  stroke: #999a9c;
}

#navigationBar {
  display: flex;
  padding: 0.133rem 0.5rem;
  align-items: center;
  justify-content: space-between;
  z-index: 5;

  background: #64656e;
  box-shadow: 1px 1px 3px 0 rgba(0, 0, 0, .2);
}

#navigationBar .hidden {
  visibility: hidden;
  width: 65px;
}

#navigationBar button {
  height: 25px;
  width: 65px;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;

  background: transparent;
  border: none;
  color: transparent;

  border-radius: 2px;
}

#navigationBar svg {
  width: 15px;
  stroke: #f8f9fb;
}

#navigationBar button span {
  padding: 5px 10px 5px 5px;
  color: #f8f9fb;
}

#navigationBar button:hover svg {
  stroke: #d4d8d9;
}

#navigationBar button:hover span {
  color: #d4d8d9;
}

#navigationBar > small {
  color: #f4f5f7;
}

.component-slider {
  align-items: center;
  display: flex;
  height: 2rem;
  margin-top: 0.5rem;
}

.component-slider .wrapper {
  display: flex;
  flex-grow: 1;
}

.component-slider input[type=range] {
  flex-grow: 1;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  width: 100%;
}

.component-slider button {
  height: 25px;
  width: 25px;
  margin: 0;
  padding: 0;

  background-color: #d4d8d9;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  color: transparent;

  border-radius: 2px;
}

.component-slider button:hover {
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);
}

.component-slider button:focus {
  outline: none;
}

.component-slider button:active {
  box-shadow: inset 1px 3px 8px 2px rgba(0, 0, 0, .1);
  outline: none;
}

.component-slider button:disabled {
  background-color: #e6ebef;
  box-shadow: none;
  color: transparent;
}

.component-graph {
  height: calc(100% - 2.5rem);
  width: 100%;

  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .05);

  border-radius: 2px;
}

#nodeInfo {
  padding-bottom: 1rem;
}

.component-node-details {
  margin: 1rem -0.5rem 0;
  padding: 0 0.5rem;
  overflow: hidden;

  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);

  border-radius: 2px;
  -webkit-transition: all ease-in 330ms;
  transition: all ease-in 330ms;
}

.component-node-details.details {
  background: #fff;
}

.component-node-details.details article {
  position: relative;
}

.component-node-details .button-bar {
  position: absolute;
  top: 1.5rem;
  right: 0;
}

.component-node-details .toggle-button {
  width: 1.4rem;
  height: 1.4rem;
  padding: 0.2rem;
  margin: 0.1rem 0.25rem 0 0;
  border: none;
  border-radius: 50rem;
  background: none;
  cursor: pointer;
}

.component-node-details .toggle-button.toggled {
  background: #e6ebef;
}

.component-node-details .toggle-button:not(:disabled):hover svg {
  stroke: #64656e;
}

.component-node-details .toggle-button:disabled {
  cursor: default;
}

.component-node-details .toggle-button:active,
.component-node-details .toggle-button:focus {
  outline: none;
}

.component-node-details.overview {
  background: #fff;
}

.component-node-details article {
  padding: 0.5rem 0 0;
}

.component-node-details article h2 {
  margin: 1rem 5px calc(1rem - 5px);
  font-size: 1.3rem;
  font-weight: 100;
}

.component-node-details strong {
  color: #343539;
  font-weight: bold;
}

.component-node-details h3 {
  margin: -0.5rem 0 1rem;
  padding: 0 5px;

  font-size: 0.833rem;
  font-weight: normal;
  text-transform: capitalize;
  opacity: 0.5;
}

.component-node-details .node-data {
  padding-top: 0.5rem;

  text-transform: capitalize;
  opacity: 1;
}

.component-node-details ul.stats {
  padding: 0 5px;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
}

.component-node-details ul.stats li {
  border-radius: 50px;
  margin-right: 0.5rem;
  padding: 0.25rem 0.5rem;

  background: #f4f5f7;

  font-size: 0.833rem;
  text-transform: capitalize;
}

.component-node-details .literal-wrapper {
  margin: 0.5rem -0.5rem 0;
  padding: 0.75rem;

  background: #f8f9fb;
  border-radius: 0 0 2px 2px;

  font-family: monospace;
  word-break: break-word;
}

.component-node-details .literal-wrapper.read-mode {
  white-space: pre-wrap;
  text-align: center;
}

.component-node-details .literal-wrapper .separator {
  display: block;
  width: 100%;
  -webkit-transition: ease-in 200ms;
  transition: ease-in 200ms;
}

.component-node-details .separator.count-0 {
  opacity: 0;
}

.component-node-details .literal-wrapper.drag .separator.count-0 {
  opacity: 0.1;
}

.component-node-details .separator-arrow {
  text-align: center;
  font-size: 1.2rem;
  opacity: 0.5;
}

.component-node-details .separator-line {
  height: 1px;
  margin: 0.5rem 0;
  opacity: 0.3;
  border-bottom: solid 1px #000;
}

.component-node-details .literal-wrapper ul {
  min-height: 1.5rem;
  margin: 0;
  padding: 0.25rem;

  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;

  list-style: none;
  border-radius: 1px;
  -webkit-transition: ease-in 200ms;
  transition: ease-in 200ms;
}

.component-node-details .literal-wrapper.drag ul {
  background: #fff;
}

.component-node-details .literal-wrapper li {
  margin: 1px;
  padding: 0.25rem 0.5rem;

  background: #fff;
  border: solid 1px #ddd;
  border-radius: 2px;

  cursor: -webkit-grab;

  cursor: grab;
}

.component-node-details small {
  display: block;
  padding: 1rem 5px 0.5rem;
  opacity: 0.5;
}


.component-graph-menu {
  margin: 0 -0.5rem 1rem;
  padding: 0.5rem;
  background: #fff;
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);
  border-radius: 2px;
}

.component-graph-menu-content {
  display: flex;
  margin-bottom: 0.25rem;
}

.component-graph-menu input[type=file] {
  display: none !important;
}

.component-graph-menu button {
  height: 50px;
  width: 50px;
  margin: 0 5px;

  border: none;
  background-color: #d4d8d9;
  background-repeat: no-repeat;
  background-position: center;
  color: transparent;

  border-radius: 2px;
}

.component-graph-menu button:hover {
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);
}

.component-graph-menu button:focus {
  outline: none;
}

.component-graph-menu button:active {
  box-shadow: inset 1px 3px 8px 2px rgba(0, 0, 0, .1);
  outline: none;
}

.component-graph-menu button:disabled {
  background-color: #e6ebef;
  box-shadow: none;
  color: transparent;
}

.component-node-menu {
  display: flex;
}

.component-node-menu button,
.component-node-menu input[type=submit] {
  height: 50px;
  width: 50px;
  margin: 0 5px;
  border: none;
  border-radius: 2px;
  background-color: #d4d8d9;
  background-repeat: no-repeat;
  background-position: center;
  color: transparent;
}

.component-node-menu button:hover,
.component-node-menu input[type=submit]:hover {
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);
}

.component-node-menu button:focus,
.component-node-menu input[type=submit]:focus {
  outline: none;
}

.component-node-menu button:active,
.component-node-menu input[type=submit]:active {
  box-shadow: inset 1px 3px 8px 2px rgba(0, 0, 0, .1);
  outline: none;
}

.component-node-menu button:disabled,
.component-node-menu input[type=submit]:disabled {
  background-color: #e6ebef;
  box-shadow: none;
  color: transparent;
}

.component-search {
  margin: 1rem -0.5rem -0.5rem;
  padding: 0 0.5rem;
  position: relative;

  background: #f8f9fb;
  border-radius: 0 0 2px 2px;
}

.component-search input {
  height: 2rem;
  width: calc(100% - 10px);
  margin: 0.25rem 5px;
  padding: 0 0.5rem;
  position: relative;
  box-sizing: border-box;
  z-index: 20;

  background: transparent;
  border: solid 1px #ddd;
  outline: none;

  line-height: 2rem;
  text-overflow: ellipsis;
}

.component-search input:first-of-type {
  margin-top: 1rem;
}

.component-search input:last-of-type {
  margin-bottom: 1rem;
}

.component-search:hover input {
  background: #fff;
  box-shadow: inset 0 1px 2px 0 rgba(0, 0, 0, .07);
}

.component-search ul:hover,
.component-search input:hover ~ ul,
.component-search input:focus ~ ul,
.component-search input:active ~ ul {
  background: #fff;
  box-shadow: 1px 3px 30px 0 rgba(0, 0, 0, .25);
}

.component-search input:disabled + ul {
  box-shadow: none;
}

.component-search ul {
  background: #f8f9fb;
  position: absolute;
  margin: calc(-8rem - 5px) 0 0;
  padding: calc(6.5rem + 10px) 0.5rem 0.5rem;
  overflow: hidden;
  border-radius: 5px;
  width: 225px;
  z-index: 10;
  max-height: 0;
}

.component-search:hover ul,
.component-search:focus ul,
.component-search:active ul {
  max-height: unset;
}

.component-search ul:hover,
.component-search ul:focus,
.component-search ul:active {
  box-shadow: 1px 3px 30px 0 rgba(0, 0, 0, .25);
}

.component-search li {
  margin: 0 -0.5rem;
  padding: 0.25rem 0.7rem;
  line-height: 1.5;

  color: #343539;

  cursor: pointer;
  font-family: monospace;
  font-size: 0.75rem;
  list-style: none;
}

.component-search li:hover {
  background: #f4f5f7;
  color: #000;

  border-radius: 2px;
}

.component-search li:first-child {
  margin-top: 0.5rem;
}

.component-search li.disabled-element:hover {
  background: transparent;
}

.component-node-card {
  margin: 0 -0.5rem;

  background: #fff;
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);

  border-radius: 2px;
}

.component-node-card > article {
  padding: 0.5rem;
}

.component-node-card > article h2 {
  margin: 1rem 5px calc(1rem - 5px);
  font-size: 1.3rem;
  font-weight: 100;
}

.app main {
  flex-direction: column;
  flex-grow: 1;
  padding: 1.5rem 1rem;

  background: #f8f9fb;
}

.app main > .graph-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;

  height: calc(100% - 2.5rem);
  width: 100%;
  border-radius: 2px;

  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .05);
  color: rgba(0, 0, 0, .5);
}

.app main > .upload-info {
  align-items: start;
  justify-content: right;
  padding: 1rem 1.5rem;
  box-sizing: border-box;

  color: #343539;
}

.app main > .upload-info > span  {
  margin-left: auto;
}

.app main > .slider-placeholder {
  margin-top: 0.5rem;
  height: 2rem;
  width: 100%;

  background: #f4f5f7;

  border-radius: 2px;
}

.app .scroll {
  overflow-y: auto;
}

.app aside {
  flex-grow: 0;
  flex-shrink: 0;
  flex-direction: column;
  margin: 1rem 0;
  padding: 0.5rem 1.5rem;
  display: flex;
  width: 240px;
}

.ReactModal__Overlay {
  z-index: 100;
}

.ReactModal__Content {
  display: flex;
  width: calc(100% - 102px) !important;
  padding: 0 !important;
  inset: 5rem 3rem 6rem !important;
  top: 5rem !important;
  left: 3rem !important;
  bottom: 6rem !important;
  border: none !important;
  border-radius: 2px !important;
  box-shadow: 5px 5px 50px 0 rgba(0, 0, 0, 0.2) !important;
  box-sizing: border-box !important;
}

.ReactModal__Content .component-graph {
  margin: 0 !important;
  padding: 1rem !important;
  box-shadow: none !important;
}

.ReactModal__Content button {
  width: calc(100% - 10px);
}

.ReactModal__Content button svg {
  height: 80%;
}

.component-passive-aside {
  flex-grow: 0;
  flex-shrink: 0;
  padding: 1rem 1.5rem !important;
  width: 234px;
  background: #f4f5f7;
}

.component-passive-aside .component-node-details.details {
  background: #fff;
}

#appWrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.component-menu {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: auto;
  background: #f4f5f7;
}

.unsupported-message {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.unsupported-message svg {
  width: 8rem;
  margin-bottom: 2rem;
  stroke: #64656e;
}

.component-menu h1 {
  margin: 0;
  padding: 2.5rem 1rem 2rem;
  text-align: center;
  color: #64656e;
}

.component-menu .editor {
  display: flex;
  flex-grow: 1;
  background: #f8f9fb;
}

.component-menu .editor-spacer {
  width: 100%;
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
  padding: 1.5rem 2rem;
  display: flex;
  flex-grow: 1;
  justify-content: space-evenly;
}


/* INPUT */

.component-menu main {
  display: flex;
  flex-direction: column;
  flex-basis: 61%;
  margin-right: 2rem;
  padding: 1.25rem 1rem;

  border-radius: 2px;
  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .05);
  color: rgba(0, 0, 0, .5);
}

.component-menu .headline-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.component-menu .headline-wrapper button {
  height: 38px;
  width: 38px;
  margin: 0;
  padding: 0;

  background-color: #d4d8d9;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  color: transparent;

  border-radius: 2px;
}

.component-menu .headline-wrapper button:hover {
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);
}

.component-menu .headline-wrapper button:focus {
  outline: none;
}

.component-menu .headline-wrapper button:active {
  box-shadow: inset 1px 3px 8px 2px rgba(0, 0, 0, .1);
  outline: none;
}

.component-menu .headline-wrapper svg {
  padding-top: 10%;
  width: 60%;
}

.component-menu .file-name {
  flex-grow: 1;
  text-align: right;
  padding: 0 1rem;
}

.component-menu h2 {
  margin: 0;
  font-size: 1.3rem;
  font-weight: 100;
}

.component-menu input[type=file] {
  display: none !important;
}

.component-menu textarea {
  resize: none;
}

.component-menu .monaco {
  flex-grow: 1;
  margin: 1rem 0 0;
  border: solid 1px #ddd;
  color: rgba(0, 0, 0, .5);
}

.component-menu .monaco .lines-content.monaco-editor-background {
  margin-top: 14px;
}

/* OPTIONS */

.component-menu aside {
  flex-basis: 37%;
}

.component-menu .options-card {
  margin: 0.5rem 0 1rem;
  padding: 1rem;
  overflow: hidden;
  border: none;
  border-radius: 2px;

  background: #fff;
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);
}

.component-menu .options-card:first-child {
  margin-top: 0;
}

.component-menu .options-card h3 {
  margin: -1rem -1rem 0;
  padding: 1.3rem 1rem calc(1.4rem - 5px);
  background: #f8f9fb;
  color: rgba(0, 0, 0, .5);
  font-weight: 100;
}

.component-menu .options-card ul {
  margin: 0.5rem 0 0;
  padding: 0;
}

.component-menu .options-card li {
  padding: 0.25rem 0;
  list-style: none;
}

.component-menu .options-card .form-label {
  display: block;
  margin: 1rem 0 0.5rem;
}

.component-menu .options-card li:first-child .form-label {
  margin-top: 0;
}

.component-menu .options-card input[type="text"],
.component-menu .options-card select {
  height: 2rem;
  width: 100%;
  padding: 0 0.5rem;
  position: relative;
  box-sizing: border-box;
  z-index: 20;
  background: transparent;
  border: solid 1px #ddd;
  box-shadow: inset 0 1px 2px 0 rgba(0, 0, 0, .07);
  outline: none;
  line-height: 2rem;
}

.component-menu input[type="checkbox"] {
  margin-right: 0.5rem;
}


/* RUN SECTION */

.component-menu .run-menu {
  padding: 2rem 2rem 3rem;
  text-align: center;
  background: #f4f5f7;
}

.component-menu .fake-button {
  margin: 0 5px;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 2px;

  background: #64656e;
  color: #f8f9fb;
  text-decoration: none;
  font-size: 0.8rem;
}

.component-menu .fake-button:hover {
  color: #d4d8d9;
  box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, .1);
}

.component-menu .fake-button:active {
  box-shadow: inset 1px 3px 8px 2px rgba(0, 0, 0, .1);
}

